function sliderCurrencies() {
    new Swiper(".currencies .mySwiper",{
        slidesPerView: 6,
        spaceBetween: 20,
        pagination: {
            el: ".currencies .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 2,
                spaceBetween: 10,
            },
            361: {
                slidesPerView: 3,
                spaceBetween: 10,
            },
            451: {
                slidesPerView: 4,
                spaceBetween: 10,
            },
            577: {
                slidesPerView: 5,
                spaceBetween: 10,
            },
            600: {
                slidesPerView: 6,
                spaceBetween: 10,
            },
            651: {
                slidesPerView: 6,
                spaceBetween: 20,
            }
        }
    })
}

$(document).ready(function () {
    sliderCurrencies();

    //Menu
    $('.burger').click(function () {
        $('.header__menu').addClass('header__menu--active')
        $('body').addClass('fixed-body')
    })

    $('.header__menu-close').click(function () {
        $('.header__menu').removeClass('header__menu--active')
        $('body').removeClass('fixed-body')
    })

    $('.header__menu-item a').click(function () {
        $('.header__menu').removeClass('header__menu--active')
        $('body').removeClass('fixed-body')
    })

    // Numbers


    //Animation
    gsap.registerPlugin(ScrollTrigger);

    //main
    const title = gsap.timeline({defaults: {duration: 0.7}})
    title.from('.main__items', {opacity: 0, x: -200})

    const arrows = gsap.timeline({defaults: {duration: 0.5}})
    arrows.from('.scroll-down__arrow', {opacity: 0, y: -10})
        .from('.scroll-down__arrow2', {opacity: 0, y: -10})
        .from('.scroll-down__arrow3', {opacity: 0, y: -10})
        .from('.scroll-down__arrow4', {opacity: 0, y: -10})
        .from('.scroll-down__arrow5', {opacity: 0, y: -10})
        .from('.scroll-down__arrow6', {opacity: 0, y: -10})
        .from('.scroll-down__arrow7', {opacity: 0, y: -10})
        .from('.scroll-down__arrow8', {opacity: 0, y: -10})
        .from('.scroll-down__arrow9', {opacity: 0, y: -10})
        .from('.scroll-down__arrow10', {opacity: 0, y: -10})
        .from('.scroll-down__arrow11', {opacity: 0, y: -10})
        .from('.scroll-down__arrow12', {opacity: 0, y: -10})

    //scheme
    const line = gsap.timeline({
        scrollTrigger: {
            trigger: '.scheme__line',
            start: 'top 100%',
            end: 'top 30%',
        },
        defaults: {duration: 0.7},
    })

    line.from('.scheme__line', {opacity: 0, y: 200})
        .from('.scheme__line2', {opacity: 0, y: 200})
        .from('.scheme__line3', {opacity: 0, y: 200})
        .from('.scheme__line4', {opacity: 0, y: 200})
        .from('.scheme__line5', {opacity: 0, y: 200})
        .from('.scheme__line6', {opacity: 0, y: 200})
        .from('.scheme__line7', {opacity: 0, y: 200})
        .from('.scheme__line8', {opacity: 0, y: 200})
        .from('.scheme__line9', {opacity: 0, y: 200})

    //connection
    const title2 = gsap.timeline({
        scrollTrigger: {
            trigger: '.connection__title',
            start: 'top 100%',
            end: 'top 30%',
        },
        defaults: { duration: 0.7 }
    });

    title2.from('.connection__title', { opacity: 0, y: 200 });

    // Modal
    $("[data-modal]").click(function () {
        let modalId = $(this).attr("data-modal")

        openModal(modalId)
    })

    function openModal(modalId){
        $(`#${modalId}`).addClass("modal--active")
        $("body").addClass("fixed-body")
    }

    $(".modal").click(function () {
        closeModal()
    })

    $("._close-modal").click(function () {
        closeModal()
    })

    $(".modal__dialog").click(function (e) {
        e.stopPropagation()
    })


    function closeModal() {
        $('.modal').removeClass("modal--active")
        $("body").removeClass("fixed-body")
    }


})